import React, { useEffect, useReducer } from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import Photo from "entities/Photo";

const NiceLoadingImage: React.FC<{ image: Photo }> = ({ image }) => {
  const forceUpdateRa = React.useReducer(() => ({}), undefined)[1] as () => void
  useEffect(() => {
    useForceUpdate();
  }, [image]);
  return <LazyLoadImage alt={image.key} effect='blur' src={image.imgSrc} width='100%' />;
};

export default NiceLoadingImage;
